<style>
    * {
        margin: 0;
        padding: 0;
    }
    body{
        color: #333;
        font-size: 16px;
        min-width: 1100px;
        overflow-x: auto;
        background-color: #f0f3f6;
        -webkit-font-smoothing: antialiased;
        font-family: PingFang SC,Helvetica Neue,Arial,sans-serif;
    }
    body, html {
        width: 100%;
        height: 100%;
    }

    #app {
        width: 100%;
        height: 100%;
        text-align: center;
    }
</style>



